<template>
  <div>
    <el-drawer
      title="Editar Lead"
      :visible.sync="drawerShow"
      :direction="direction"
      :before-close="handleClose"
      size="50%"
    >
      <div v-loading="this.loading" style="padding: 0px 20px; box-sizing: border-box;">
        <ul id="leadDataUpdate">
          <li v-for="field in this.leadData.fields" :key="field.field_id">
            <span class="label">{{ field.field_label }}</span>
            <span class="field">
              <el-input
                v-if="textField(field.type_field)"
                v-model="leadData.form[field.field_table_name]"
              ></el-input>

              <el-switch
                v-if="switchField(field.type_field)"
                v-model="leadData.form[field.field_table_name]"
              ></el-switch>
              <el-date-picker
                v-if="dateField(field.type_field)"
                v-model="leadData.form[field.field_table_name]"
                type="date"
                format="dd/MM/yyyy"
              ></el-date-picker>
              <el-select
                v-model="leadData.form[field.field_table_name]"
                width="100%"
                v-if="selectField(field.type_field)"
                clearable
                placeholder="Selecione"
              >
                <el-option
                  v-for="item in leadData.fieldsOptions[field.field_table_name]"
                  :key="item.dropdown_option_value"
                  :label="item.dropdown_option_value"
                  :value="item.dropdown_option_value"
                ></el-option>
              </el-select>
            </span>
          </li>
        </ul>
        <el-button @click="saveLead()" type="primary">Atualizar Dados</el-button>
      </div>
    </el-drawer>
  </div>
</template>

<script>
export default {
  name: "LeadUpdate",
  data() {
    return {
      loading: false,
      direction: "rtl",
      leadData: {
        data: [],
        fields: [],
        form: {},
        fieldsOptions: {},
      },
    };
  },
  props: {
    drawerShow: {
      default: false,
    },
    leadID: {
      default: 0,
    },
  },
  methods: {
    textField(typeField) {
      if (typeField === "text") {
        return true;
      } else {
        return false;
      }
    },
    switchField(typeField) {
      if (typeField === "switch") {
        return true;
      } else {
        return false;
      }
    },
    dateField(typeField) {
      if (typeField === "date") {
        return true;
      } else {
        return false;
      }
    },
    selectField(typeField) {
      if (typeField === "dropdown") {
        return true;
      } else {
        return false;
      }
    },
    saveLead() {
      var fields = this.leadData.fields;
      var newData = this.leadData.form;
      var oldData = this.leadData.data;

      var is_valid = false;
      fields.forEach((field) => {
        if (
          newData[field.field_table_name] != oldData[field.field_table_name]
        ) {
          is_valid = true;
          return false;
        }
      });

      if (is_valid == false) {
        this.leadData.form = {};
        this.$emit("done");
      } else {
        this.XHRSaveLead();
      }
    },
    XHRSaveLead() {
      this.loading = true;
      var newData = this.leadData.form;
      this.$http
        .put("/leads/" + this.leadID, newData)
        .then((res) => {
          this.loading = false;
          this.$notify.success({
            title: "Sucesso",
          });
          this.$emit("done");
          this.$emit("reload");
          res;
        })
        .catch((err) => {
          this.loading = false;
          err;
          this.$notify.error({
            title: "Erro na soliciatação",
          });
        });
    },
    handleClose(done) {
      this.$confirm("Você tem certeza que não deseja atualizar este lead?")
        .then((e) => {
          this.$emit("done");
          done(e);
        })
        .catch((e) => {
          e;
        });
    },
    getOldDataFromLead() {
      this.$http
        .get("/leads/" + this.leadID)
        .then((res) => {
          // PEGA OS DADOS DO LEAD
          this.leadData.data = res.data;

          // PEGA OS CAMPOS
          this.$http
            .get("/fields/leads")
            .then((resFields) => {
              this.leadData.fields = resFields.data;
              var d = resFields.data;
              d.forEach((element) => {

                if (element.type_field === "dropdown") {
                  this.$http
                    .get("/fields/dropdown-values/" + element.field_id)
                    .then((df) => {
                      this.$set(
                        this.leadData.fieldsOptions,
                        element.field_table_name,
                        df.data
                      );
                    });
                }

                this.$set(
                  this.leadData.form,
                  element.field_table_name,
                  this.leadData.data[element.field_table_name]
                );
              });
            })
            .catch((errF) => {
              this.$notify.error({
                title: "Erro nos Campos",
                message: errF.response.data,
              });
            });
        })
        .catch((err) => {
          this.$notify.error({
            title: "Erro",
            message: err.response.data,
          });
        });
    },
  },
  watch: {
    drawerShow(newEstate, oldEstate) {
      if (newEstate == true) {
        this.getOldDataFromLead();
      } else {
        oldEstate;
      }
    },
  },
};
</script>

<style lang="scss">
#leadDataUpdate {
  margin: 0px;
  padding: 0px;
  li {
    list-style: none;
    padding-bottom: 20px;

    span {
      display: inline-block;

      &.label {
        width: 40%;
        font-weight: 600;
      }
    }
  }
}
</style>