<script>
import UpdateLead from "@/views/restricted/Leads/Update";
export default {
  name: "ViewLead",
  components: { UpdateLead },
  data() {
    return {
      drawerShow: false,
      eventDrawer: {
        show: false,
        title: "",
        subtitle: "",
        source: "",
        date: "",
        content: "",
      },
      events: [
        {
          event_id: 1,
          date: "2018/04/12 10:15",
          title: "E-mail enviado",
          subtitle: "Envio de contrato GuiaSales",
          description: "Olá mundo lindo",
          source: "user",
          source_name: "Renan Joppert",
          type: "email",
        },
      ],
      num: 30,
      leadRequest: -1,
      leadData: {
        data: [],
        fields: [],
      },
      categoryActivity: {
        list: [
          {
            value: "events",
            label: "Eventos",
          },
          {
            value: "emails",
            label: "E-mails",
          },
          {
            value: "calls",
            label: "Chamadas",
          },
          {
            value: "tasks",
            label: "Tarefas",
          },
        ],
        current: "events",
      },
      colorLeadScore: "#A3A1FB",
      buttons: [],
    };
  },
  mounted() {
    this.getLeadData();
  },
  methods: {
    toggleUpdateLead() {
      this.drawerShow = !this.drawerShow;
    },
    getLeadData() {
      this.leadRequest = this.$route.params.id;

      this.$http.get("/buttons/view/leads").then((res) => {
        this.buttons = res.data;
      });

      this.$http.get("/leads/" + this.leadRequest).then((res) => {
        if (res.status != 200) {
          this.$notify.error({
            title: "Erro",
            message: res.data,
          });
        } else {
          // PEGA OS DADOS DO LEAD
          this.leadData.data = res.data;

          // PEGA OS CAMPOS
          this.$http.get("/fields/leads").then((resFields) => {
            if (res.status == 200) {
              this.leadData.fields = resFields.data;
            }
          });
        }
      });
    },
    deleteLead() {
      this.$http.delete("/leads/" + this.leadRequest).then((res) => {
        if (res.status == "200") {
          this.$router.push({ name: "Leads_List" });
        }
      });
    },
    EventTypeIcon(type) {
      if (type === "email") {
        return "el-icon-message";
      }
    },
    eventSourceIcon(source) {
      if (source === "user") {
        return "fas fa-user";
      }
    },
    openEventsDrawer(eventData) {
      this.eventDrawer.title = eventData.title;
      this.eventDrawer.source = eventData.source;
      this.eventDrawer.date = eventData.date;
      this.eventDrawer.content = eventData.description;
      this.eventDrawer.show = true;
    },
    labelLeadScore() {
      return "0 Lead Score";
    },
    currentCategoryClass(category) {
      if (category === this.categoryActivity.current) {
        return "current";
      } else {
        return;
      }
    },
    changeCategoryEvents(category) {
      this.categoryActivity.current = category;
      return;
    },
    valueOfField(table_name) {
      return table_name;
    },
  },
};
</script>

